import React, {Fragment} from 'react'
import {Avatar, Button, CellButton, MiniInfoCell, RichCell, Separator, Subhead, Banner, Caption} from "@vkontakte/vkui";
import Icon24Delete from '@vkontakte/icons/dist/24/delete';

const CartListItems = ({ order, onDeletePosition, onDecrementPosition, onIncrementPosition, clearCart, changeCount }) => {
    let arr = [];
    for (let key in order) {
        if (key !== 'meta'){
        arr.push(
            <React.Fragment key={key}>
                <Banner
                    before={<Avatar size={96} mode="image" src={order[key].item.thumb_photo} />}
                    header={order[key].item.title}
                    subheader={`Цена: ${order[key].item.varPrice.find(productVar=>productVar.active === true).price} руб.`}
                    asideMode="dismiss"
                    actions={
                        <>
                            <div style={{display: 'flex'}}>
                                <span>{order[key].item.varPrice[0].countLabel}: </span>
                                {
                                    order[key].item.varPrice.map((item, index)=>{
                                        return <button
                                            onClick={()=>changeCount(order[key].item, index)}
                                            style={{color: item.active === true && '#41ca41', float: 'left', margin: 'auto'}}
                                            stretched
                                        >{item.count}</button>
                                    })
                                }
                            </div>
                        <div
                            style={{display: 'flex'}}
                        >
                            <Button
                                mode={'overlay_outline'}
                                onClick={() => {
                                    onDecrementPosition(order[key].item);
                                }}
                                size={'m'}
                                style={{float: 'left', margin: 5, color: '#482d06'}}
                            >-1</Button>
                            <span style={{margin: 'auto', fontSize: 25, fontWeight: 'bold'}}>&nbsp;{key in order ? order[key].count : 0}&nbsp;</span>
                            <Button
                                mode={'overlay_outline'}
                                onClick={() => {
                                    onIncrementPosition(order[key].item);
                                }}
                                size={'m'}
                                style={{float: 'left', margin: 5, color: '#482d06'}}
                            >+1</Button>
                        </div>
                        </>
                    }
                    onDismiss={()=>onDeletePosition(order[key].item)}
                />
                    {/*<RichCell*/}
                    {/*    key={key}*/}
                    {/*    before={<Avatar size={92} src={order[key].item.thumb_photo}/>}*/}
                    {/*    text={*/}
                    {/*        <MiniInfoCell after={<Button mode={'outline'} onClick={()=>onDeletePosition(order[key].item)} style={{color: 'red'}}>Удалить</Button>}>*/}
                    {/*            <button*/}
                    {/*                className="Place__food-button"*/}
                    {/*                onClick={() => {*/}
                    {/*                    onDecrementPosition(order[key].item);*/}
                    {/*                }}*/}
                    {/*            >*/}
                    {/*                -*/}
                    {/*            </button>*/}
                    {/*            <span>&nbsp;{key in order ? order[key].count : 0}&nbsp;</span>*/}
                    {/*            <button*/}
                    {/*                className="Place__food-button"*/}
                    {/*                onClick={() => {*/}
                    {/*                    onIncrementPosition(order[key].item);*/}
                    {/*                }}*/}
                    {/*            >*/}
                    {/*                +*/}
                    {/*            </button>*/}
                    {/*        </MiniInfoCell>*/}
                    {/*    }*/}
                    {/*    bottom={<MiniInfoCell><Subhead>Цена: {order[key].item.varPrice.find(productVar=>productVar.active === true).price} руб.</Subhead></MiniInfoCell>}*/}
                    {/*    caption={*/}
                    {/*        <MiniInfoCell>*/}
                    {/*            {*/}
                    {/*                order[key].item.varPrice.map((item, index)=>{*/}
                    {/*                    return <button onClick={()=>changeCount(order[key].item, index)} style={{color: item.active === true && '#41ca41'}}>{item.count}</button>*/}
                    {/*                })*/}
                    {/*            }*/}
                    {/*        </MiniInfoCell>*/}
                    {/*    }*/}
                    {/*>*/}
                    {/*    <MiniInfoCell textWrap={'full'}>{order[key].item.title}</MiniInfoCell>*/}
                    {/*</RichCell>*/}
                <Separator/>
            </React.Fragment>
        )}
    }
    // if (arr.length !== 0) arr.push(<CellButton mode={"danger"} before={<Icon24Delete/>} onClick={()=>clearCart()}>Очистить корзину</CellButton>)
    return arr
};
export default CartListItems